.archive-grid {
  margin: 130px auto;

  .illustrator-name {
    color: $off-black;
  }

  .bricklayer-column-sizer {
    width: 25%;

    @media screen and (max-width: $medium-vp) {
      width: 33.3333333333%;
    }

    @media screen and (max-width: $small-vp) {
      width: 50%;
    }

    @media screen and (max-width: $smallest-vp) {
      width: 100%;
    }
  }

  @media screen and (max-width: $small-vp) {
    width: 100vw;
    padding: 20px;
    margin: 60px 0 0;
  }

  @media screen and (max-width: $smallest-vp) {
    flex-direction: column;
  }

  .illustrator-meta-container {
    margin: 0.67em 0;
    font-family: $monospace;
  }

  .illustrator-title {
    margin-top: 0.5em;
    color: rgba($off-black, 0.75);
  }

  .gallery-item {
    margin: 0 12% 20%;

    @media screen and (max-width: $small-vp) {
      margin-right: 5%;
      margin-left: 0;
    }
  }

  .illustrator-link {
    display: block;
    padding-bottom: 1em;
  }

  .illustrator-link:hover {
    .illustrator-title {
      color: $color-accent;
    }
  }

  .bricklayer-column:last-child {
    .gallery-item {
      @media screen and (max-width: $small-vp) {
        margin-right: 0;
        margin-left: 5%;
      }

      @media screen and (max-width: $smallest-vp) {
        margin: 0;
      }
    }
  }
}

.section-indicator-index {
  font-family: $sans-serif-light;
  color: $color-accent;
  position: fixed;
  top: 0;
  left: 0;
  z-index: $z-behind;
  width: 100%;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  @include rsp-font(14vw, 24px);
}

.section-indicator-single {
  width: 100%;
  padding: 1em 30px;
}

.page-inner {
  max-width: 60vw;
  margin: 130px 0 0 30px;

  line-height: 1.2em;
  @include rsp-font(1vw, 14px);

  @media screen and (max-width: $small-vp) {
    padding: 0 20px;
    max-width: none;
    margin: 8em 0 0;
  }

  .entry-title {
    line-height: 1em;
    margin: 1em 0;
    font-family: $sans-serif-light;
    @include rsp-font(5vw, 50px);
  }

  .entry-title-secondary {
    color: $site-font-color;
    line-height: 1em;
    margin: 1.5em 0;
    @include rsp-font(1.5vw, 25px);
  }

  .col-container {
    color: #555;
  }

  .faculty-list {
    max-width: 70%;

    @media screen and (max-width: $small-vp) {
      max-width: none;
    }
  }
}

#post-6 {
  max-width: 60vw;

  @media screen and (max-width: $small-vp) {
    padding: 0 20px;
    max-width: none;
    margin-top: 8em;
  }
}

.faculty-list {
  display: flex;

  .span1 {
    width: 50%;
  }
}
