figure {
  margin: 0;

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  .wp-caption-text {
    display: none;
    opacity: 0;
  }
}

.illustrator-image {
  position: relative;

  .wp-post-image {
    position: absolute;
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.image-modal-container-full-image {
  position: relative;
  z-index: $z-middle;
  max-width: 100%;
  max-height: 100%;
  user-select: none;
  object-fit: contain;
}

.gallery-icon-anchor {
  position: relative;
  display: block;
  cursor: zoom-in;

  img {
    margin: 0 auto;
  }
}

.image-modal-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $z-foreground;
  overflow: auto;
  background: $site-background;

  @media screen and (max-width: $medium-vp) {
    left: 0;
  }

  .image-modal-image {
    position: absolute;
    top: 2%;
    right: 1%;
    bottom: 75px;
    left: 1%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .image-modal-caption {
    position: absolute;
    right: 20px;
    bottom: 20px;
    z-index: $z-foreground;
    max-width: 350px;
    padding: 1em 0 0 1em;
    font-size: 0.95em;
    line-height: 1.3em;
    background: $site-background;

    @media screen and (max-width: $ipad-vp) {
      bottom: 0;
      left: 20px;
      padding: 1em;
    }

    @media screen and (max-width: $small-vp) {
      bottom: 75px;
    }

  }

  .close-panel {
    position: fixed;
  }

  .close-btn {
    path {
      stroke: $color-accent;
    }
  }
}

.lazyload {
  opacity: 0;
}

.bricklayer-column .lazyload {
  opacity: 1;
}

.lazyloaded {
  &.blur-up {
    filter: blur(0);
  }
}

.blur-up {
  filter: blur(5px);
  transition: filter 300ms ease-in-out, opacity 100ms ease-in-out;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
}

.lazyload-image-placeholder {
  display: block;
  width: 100%;
  height: auto;
}
